import React from "react";
import key from "../../../util/key";
import Context from "../../../Context";
import { injectIntl } from "react-intl";
import { withRouter } from "react-router-dom";
import Layout4 from "../../../util/Layout4";
import paramsHasChanged from "../../../util/paramsHasChanged";
import {
  Tree,
  Button,
  Typography,
  Popconfirm,
  Space,
  Tooltip,
  message,
  Form,
  Modal,
  Input,
  Select,
} from "antd";
import { PlusOutlined, DeleteOutlined, BuildOutlined } from "@ant-design/icons";
import RegistryValues from "./RegistryValues";
import Title from "../Title";
const { Option } = Select;
const { Text } = Typography;

class Registry extends React.Component {
  static contextType = Context;
  constructor(props) {
    super(props);
    this.state = {
      add: false,
      treeSearch: "",
      treeKey: key(),
      valuesKey: key(),
      keys: {},
      tree: [],
      values: [],
    };
  }

  pendingStart = () => {
    if (this.props.onPendingStart) {
      this.props.onPendingStart();
      return;
    }
    this.setState({ pending: true });
  };
  pendingEnd = () => {
    if (this.props.onPendingEnd) {
      this.props.onPendingEnd();
      return;
    }
    this.setState({ pending: false });
  };

  componentDidMount() {
    this.setParams();
    this.load();
  }

  componentDidUpdate(prevProps) {
    if (paramsHasChanged(prevProps, this.props)) {
      this.setParams();
    }

    if (prevProps.intl.locale !== this.props.intl.locale) {
      this.load();
    }
  }

  setParams = () => {
    const { param1: nodeId } = this.props.match.params;
    this.setState({
      nodeId,
      valuesKey: key(),
    });
    if (!nodeId) {
      this.setState({ treeKey: key() });
    }
  };

  getNode = () => {
    const { keys, nodeId } = this.state;
    return keys[nodeId] || {};
  };

  load = async () => {
    this.pendingStart();
    let keys = await this.context.api.q(
      "/registry/key",
      {},
      { toObject: true }
    );

    let id_CURRENT_CONFIG = 0;
    let id_CURRENT_USER = 0;

    let values = [];
    let valuesQuery = await this.context.api.q(
      "/registry/value",
      {},
      { toArray: true }
    );

    valuesQuery.forEach((value) => {
      if (value.keyname === "CURRENT_CONFIG") id_CURRENT_CONFIG = value.key;
      if (value.keyname === "CURRENT_USER") id_CURRENT_USER = value.key;
      values.push({ ...value, path: this.buildPath(value.subkey, keys) });
    });

    let defaultTreeItems = [];
    if (id_CURRENT_CONFIG) {
      keys[id_CURRENT_CONFIG] = {
        id: id_CURRENT_CONFIG,
        root: id_CURRENT_CONFIG,
        key: "CURRENT_CONFIG",
        level: 0,
      };
      defaultTreeItems.push({
        id: id_CURRENT_CONFIG,
        root: id_CURRENT_CONFIG,
        parent: null,
        key: "CURRENT_CONFIG",
        level: 0,
      });
    }
    if (id_CURRENT_USER) {
      keys[id_CURRENT_USER] = {
        id: id_CURRENT_USER,
        root: id_CURRENT_USER,
        key: "CURRENT_USER",
        level: 0,
      };
      defaultTreeItems.push({
        id: id_CURRENT_USER,
        root: id_CURRENT_USER,
        parent: null,
        key: "CURRENT_USER",
        level: 0,
      });
    }

    let tree = await this.context.api.q(
      "/registry/key",
      {},
      { toTree: true, defaultTreeItems }
    );

    this.setState({
      keys,
      tree,
      values,
      treeKey: key(),
      valuesKey: key(),
      pending: false,
    });
    this.pendingEnd();
  };

  onTreeSelect = (selectedKeys) => {
    if (Array.isArray(selectedKeys)) {
      if (selectedKeys[0]) {
        this.props.history.push(`/admin/registry/${selectedKeys[0]}/`);
      }
    }
  };

  buildPath = (subkey, keys) => {
    let path = [];
    if (keys[subkey]) {
      let { parent, level, key } = keys[subkey];
      path.push(key);
      while (level > 1 && parent) {
        let parentItem = keys[parent];
        if (parentItem) {
          level = parentItem.level;
          parent = parentItem.parent;
          path.push(parentItem.key);
        } else {
          break;
        }
      }
    }
    return path.reverse();
  };

  onDelete = async () => {
    const { intl } = this.props;
    this.pendingStart();
    const node = this.getNode();
    const root = this.state.keys[node.root] || {};

    let arPath = this.buildPath(node.id, this.state.keys);
    let sPath = Array.isArray(arPath) ? arPath.join("\\") : "";
    let result = await this.context.api.q("/registry/delete/tree", {
      key: root.key,
      subkey: sPath,
    });

    if (result !== false) {
      message.success(intl.formatMessage({ id: "delete.success" }));
      this.load();
    } else {
      message.error(intl.formatMessage({ id: "delete.fail" }));
      this.pendingEnd();
    }
  };
  onAddStart = () => {
    this.setState({ add: true });
  };

  onAddEnd = async (formData) => {
    const { intl } = this.props;
    this.pendingStart();
    let saveData = {
      ...formData,
    };

    let result = await this.context.api.q("/registry/write", saveData);

    //Check result
    if (result === false) {
      //Error...
      this.pendingEnd();
      message.error(intl.formatMessage({ id: "save.fail" }));
    } else {
      //Success...
      message.success(intl.formatMessage({ id: "save.success" }));
      this.closeModals();
      this.load();
    }
  };

  closeModals = () => {
    this.setState({ add: false, edit: false });
  };

  m = (key, prefix = "admin.registry.") => {
    const { intl } = this.props;
    return intl.formatMessage({ id: prefix + key });
  };

  render() {
    const { intl } = this.props;
    const { tree, treeKey, nodeId, keys } = this.state;
    const { valuesKey, values, formKey, add } = this.state;
    const node = this.getNode();
    const root = keys[node.root] || {};
    const path = this.buildPath(node.id, keys).join("\\");
    return (
      <>
        <Title
          title={this.m("registry")}
          url="/admin/registry"
          icon={<BuildOutlined />}
        />
        {add && (
          <Modal onCancel={this.closeModals} visible footer={null}>
            <Form
              key={formKey}
              layout="vertical"
              name="type"
              initialValues={{
                name: "",
                data: "",
                type: "",
                subkey: path || "",
                key: root.key || "",
              }}
              onFinish={this.onAddEnd}
            >
              <Space direction="vertical" style={{ width: "100%" }}>
                <Form.Item
                  rules={[{ required: true }]}
                  label={this.m("key")}
                  name="key"
                >
                  <Select>
                    <Option value="CURRENT_CONFIG">CURRENT_CONFIG</Option>
                    <Option value="CURRENT_USER">CURRENT_USER</Option>
                  </Select>
                </Form.Item>
                <Form.Item
                  rules={[{ required: true }]}
                  label={this.m("subkey")}
                  name="subkey"
                >
                  <Input />
                </Form.Item>

                <Form.Item
                  rules={[{ required: true }]}
                  label={this.m("name")}
                  name="name"
                >
                  <Input />
                </Form.Item>

                <Form.Item
                  rules={[{ required: true }]}
                  label={this.m("type")}
                  name="type"
                >
                  <Select>
                    <Option value="0">{this.m("integer")}</Option>
                    <Option value="1">{this.m("number")}</Option>
                    <Option value="2">{this.m("datetime")}</Option>
                    <Option value="3">{this.m("string")}</Option>
                    <Option value="4">{this.m("boolean")}</Option>
                  </Select>
                </Form.Item>

                <Form.Item label={this.m("data")} name="data">
                  <Input />
                </Form.Item>

                <Form.Item style={{ marginTop: "20px" }}>
                  <Space>
                    <Button
                      loading={this.state.pending}
                      type="primary"
                      htmlType="submit"
                    >
                      {intl.formatMessage({
                        id: "save",
                      })}
                    </Button>
                    <Button onClick={this.closeModals}>
                      {intl.formatMessage({
                        id: "cancell",
                      })}
                    </Button>
                  </Space>
                </Form.Item>
              </Space>
            </Form>
          </Modal>
        )}
        <Layout4 pending={this.state.pending}>
          <div>
            {node.key && (
              <>
                <Text type="secondary">{node.key}</Text>
              </>
            )}
          </div>
          <div className="isoNoteBtns">
            <Space>
              <Tooltip title={this.m("btn.add")}>
                <Button
                  onClick={this.onAddStart}
                  type="primary"
                  icon={<PlusOutlined />}
                />
              </Tooltip>
              {nodeId && (
                <Popconfirm
                  title={`${intl.formatMessage({
                    id: "delete.sure",
                  })}`}
                  onConfirm={() => this.onDelete()}
                  okText={intl.formatMessage({ id: "yes" })}
                  cancelText={intl.formatMessage({ id: "no" })}
                >
                  <Tooltip title={this.m("btn.delete")}>
                    <Button icon={<DeleteOutlined />} />
                  </Tooltip>
                </Popconfirm>
              )}
            </Space>
          </div>
          <Tree
            key={treeKey}
            defaultExpandParent
            defaultSelectedKeys={[nodeId]}
            defaultExpandedKeys={[nodeId]}
            treeData={tree}
            onSelect={this.onTreeSelect}
          />
          <RegistryValues
            onPendingStart={this.pendingStart}
            onPendingEnd={this.pendingEnd}
            onAfterChange={this.load}
            key={valuesKey}
            values={values}
            node={node}
            nodeId={nodeId}
          />
        </Layout4>
      </>
    );
  }
}

export default withRouter(injectIntl(Registry));
