import React from "react";
import Context from "../../../Context";
import { injectIntl } from "react-intl";
import {
  Alert,
  Button,
  Modal,
  Form,
  Select,
  Popconfirm,
  Space,
  message,
  Table,
  Typography,
  Input,
} from "antd";
import Flex from "../../../util/Flex";
import { EditOutlined, DeleteOutlined } from "@ant-design/icons";

const { Option } = Select;
const { Text } = Typography;

class RegistryValues extends React.Component {
  static contextType = Context;
  constructor(props) {
    super(props);
    this.state = {
      pending: false,
      edit: false,
      item: {},
    };
  }
  pendingStart = () => {
    this.setState({ pending: true });
    if (this.props.onPendingStart) {
      this.props.onPendingStart();
      return;
    }
  };
  pendingEnd = () => {
    this.setState({ pending: false });
    if (this.props.onPendingEnd) {
      this.props.onPendingEnd();
      return;
    }
  };

  showItemValue = (item) => {
    const { intl } = this.props;
    let value = item.value || {};
    let displayValue = "";
    if (value.vtype === 0) displayValue = value.vinteger;
    if (value.vtype === 1) displayValue = value.vnumeric;
    if (value.vtype === 2) {
      let date = new Date(value.vdatetime);
      displayValue = `${date.toLocaleString()}`;
    }
    if (value.vtype === 3) displayValue = value.vstring;
    if (value.vtype === 4)
      displayValue =
        value.vboolean === true
          ? intl.formatMessage({ id: "yes" })
          : intl.formatMessage({ id: "no" });
    return displayValue;
  };

  onEditStart = (item) => {
    this.setState({ item, edit: true });
  };
  onEditEnd = () => {
    this.setState({ edit: false });
  };
  onDelete = async (item) => {
    const { intl } = this.props;

    if (item) {
      this.pendingStart();
      let sPath = Array.isArray(item.path) ? item.path.join("\\") : "";
      let result = await this.context.api.q("/registry/delete/value", {
        key: item.keyname,
        subkey: sPath,
        name: item.valuename,
      });
      if (result !== false) {
        if (this.props.onAfterChange) {
          this.props.onAfterChange();
        }
        message.success(intl.formatMessage({ id: "delete.success" }));
      } else {
        this.pendingEnd();
        message.error(intl.formatMessage({ id: "delete.fail" }));
      }
    }
  };

  save = async (data) => {
    const { intl } = this.props;
    const { item } = this.state;
    let sPath = Array.isArray(item.path) ? item.path.join("\\") : "";

    this.pendingStart();
    let result = await this.context.api.q("/registry/write", {
      key: item.keyname,
      subkey: sPath,
      name: item.valuename,
      type: item.value.vtype,
      data: data.value,
    });

    if (result !== false) {
      this.onEditEnd();
      if (this.props.onAfterChange) {
        this.props.onAfterChange();
      }
      message.success(intl.formatMessage({ id: "save.success" }));
    } else {
      message.error(intl.formatMessage({ id: "save.fail" }));
      this.pendingEnd();
    }
  };

  m = (key, prefix = "admin.registry.") => {
    const { intl } = this.props;
    return intl.formatMessage({ id: prefix + key });
  };

  render() {
    const { intl } = this.props;
    const { edit, item } = this.state;
    const columns = [
      {
        title: this.m("valuename"),
        dataIndex: "valuename",
        key: "valuename",
        render: (value) => <Text type="secondary">{value}</Text>,
        width: "50%",
      },
      {
        title: this.m("value"),
        dataIndex: "value",
        key: "value",
        render: (value, item) => (
          <Flex between>
            <div>{this.showItemValue(item)}</div>
            <Space>
              <Button
                onClick={() => this.onEditStart(item)}
                icon={<EditOutlined />}
              />
              <Popconfirm
                title={`${intl.formatMessage({
                  id: "delete.sure",
                })} ${item.valuename}`}
                onConfirm={() => this.onDelete(item)}
                okText={intl.formatMessage({ id: "yes" })}
                cancelText={intl.formatMessage({ id: "no" })}
              >
                <Button icon={<DeleteOutlined />} />
              </Popconfirm>
            </Space>
          </Flex>
        ),
      },
    ];

    const initialValues = this.props.values || [];
    let values = initialValues.filter(
      (item) => item.subkey === this.props.nodeId
    );

    let sPath = Array.isArray(item.path) ? item.path.join("\\") : "";
    let type = item.value ? item.value.vtype : false;

    return (
      <>
        {edit && (
          <Modal onCancel={this.onEditEnd} visible footer={null}>
            <Form
              key={item.id}
              layout="vertical"
              name="registryItem"
              initialValues={{
                value: this.showItemValue(item),
              }}
              onFinish={this.save}
            >
              <Space direction="vertical" style={{ width: "100%" }}>
                <Alert
                  style={{ marginBottom: "15px" }}
                  type="info"
                  message={
                    <div>
                      {item.valuename}
                      <br />
                      {sPath}
                    </div>
                  }
                />

                <Form.Item label={this.m("value")} name="value">
                  {type === 4 ? (
                    <Select>
                      <Option value="true">
                        {intl.formatMessage({ id: "yes" })}
                      </Option>
                      <Option value="false">
                        {intl.formatMessage({ id: "no" })}
                      </Option>
                    </Select>
                  ) : (
                    <Input />
                  )}
                </Form.Item>

                <Form.Item style={{ marginTop: "20px" }}>
                  <Space>
                    <Button
                      loading={this.state.pending}
                      type="primary"
                      htmlType="submit"
                    >
                      {intl.formatMessage({
                        id: "save",
                      })}
                    </Button>
                    <Button onClick={this.onEditEnd}>
                      {intl.formatMessage({
                        id: "cancell",
                      })}
                    </Button>
                  </Space>
                </Form.Item>
              </Space>
            </Form>
          </Modal>
        )}
        <Table
          pagination={{ hideOnSinglePage: true }}
          columns={columns}
          dataSource={values}
        />
      </>
    );
  }
}
export default injectIntl(RegistryValues);
